import React, {useEffect, useState} from "react";
import style from './index.module.css'
import Story from './components/story'

const Index = (props) => {
    const [userWay, setUserWay] = useState([0])
    const [current, setCurrent] = useState({})
    useEffect(() => {
        if (Story[userWay[userWay.length-1]].style && Story[userWay[userWay.length-1]].style.backgroundImage) {
            setCurrent({
                ...current,
                style: {
                    ...current.style,
                    backgroundImage: `url("${Story[userWay[userWay.length-1]].style.backgroundImage}")`
                }
            })
        }
    }, [userWay])

    return(
        <main style={{backgroundImage:current.style && current.style.backgroundImage && current.style.backgroundImage}}>
            <div className={style.cardsList} style={{position:'relative'}}>
                {Story[userWay[userWay.length - 1]].title &&
                    <h3>{Story[userWay[userWay.length - 1]].title}</h3>
                }
                {Story[userWay[userWay.length - 1]].text &&
                    <p>{Story[userWay[userWay.length - 1]].text}</p>
                }
                <div className={style.answersList}>
                    {Story[userWay[userWay.length-1]].answers.map((button,i) =>
                        <button onClick={() => {setUserWay([...userWay, button.target])}} key={i}>{button.text}</button>
                    )}
                    {userWay.length > 1 &&
                        <button onClick={() => {setUserWay(userWay.slice(0, userWay.length - 1))}}>Вернуться</button>
                    }
                </div>
                <a href="/event/" style={{position:'absolute', bottom:0, left:0}}>home</a>
            </div>
        </main>
    )
}

export default Index